@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.file-card {
  justify-content: space-between;
  grid-template-columns: 360px 120px;
  padding: 10px 16px;
  margin-left: 0;
  margin-top: 8px;
  position: relative;
  background-color: color.adjust(pick-color('primary', 'blue'), $lightness: 45%);
  border-radius: 5px;

  &::before {
    content: '';
    position: absolute;
    height: 100%;
    border-left: 4px solid pick-color('primary', 'web');
    width: 20px;
    border-radius: inherit;
  }

  &.selected-file {
    background-color: color.adjust(pick-color('primary', 'blue'), $lightness: 40%);
  }

  span {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .fc-location {
    justify-self: flex-end;
    cursor: auto;

    svg {
      width: 22px;
      height: 22px;
      fill: pick-color('primary', 'charcoal');
      flex-shrink: 0;
    }

    .location-label {
      margin-left: 4px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &.file-not-exists {
    background-color: pick-color('primary', 'fog');
    cursor: not-allowed;
    pointer-events: none;

    &::before {
      border-left: 4px solid pick-color('primary', 'ash');
    }

    span {
      cursor: not-allowed;
      color: pick-color('primary', 'ash');
    }

    .fc-location svg {
      fill: pick-color('primary', 'ash');
    }
  }

  &.redaction-progress {
    background-color: color.adjust(pick-color('common', 'warning'), $lightness: 30%);
    cursor: not-allowed;
    pointer-events: none;

    &::before {
      border-left: 4px solid pick-color('common', 'warning');
    }

    span {
      color: pick-color('common', 'warning');
    }

    .fc-location svg {
      fill: pick-color('common', 'warning');
    }
  }
}
